import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { ValidationError } from "../ValidationError";
import { MexElement } from "./MexElement";

export class CustomType extends MexElement {
    constructor(model: BpmnModel) {
        super(model);
    }

    AssemblyName: string;
    TypeName: string;
    readonly ClrTypeID: string = "bpmn.Mex.CustomType";

    NewInstance(): CustomType {
        return new CustomType(this.Model);
    }

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.AssemblyName = null;
        this.TypeName = null;
    }

    ConvertToJsonObject(): any {
        let obj: any = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "AssemblyName", this.AssemblyName);
        BpmnModelHelper.SetAttributeValue(obj, "TypeName", this.TypeName);
        BpmnModelHelper.SetAttributeValue(obj, "ClrTypeID", this.ClrTypeID);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.AssemblyName = obj.AssemblyName;
        this.TypeName = obj.TypeName;
    }

    Validate(errors: Array<ValidationError>): void {
        super.Validate(errors);
    }
}